.addNew {
    margin: 16px;
    padding: 8px;
}

#form {
    width: 100%;
    border-radius: 5px;
    padding: 8px;
    display: flex;
    align-items: center;
    align-items: center;
    justify-content: center;
    -ms-flex-direction: column;
    flex-direction: column;
}

#input {
    width: 50%;
    padding: 6px 12px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}

#submit {
    width: 25%;
    background-color: #4caf50;
    color: white;
    padding: 12px 8px;
    margin: 8px 0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

label {
    margin-bottom: 10px;
}
